const menuBtn = document.querySelector('.menu') 
const menu = document.querySelector('menu') 
const closeBtn = menu.querySelector('.close-btn') 
const menuItems = menu.querySelectorAll('ul>li>a') 
const modal = document.querySelector('.popup')
const buttons = document.querySelectorAll('.popup-btn')
const popupContent = modal.querySelector('.popup-content')



// let count = 0
// let idInterval

//    :
buttons.forEach(btn => { 
        btn.addEventListener('click', () => {
            modal.style.display = 'block'
        })
})
//       :  
const handlMenu = () => { 
    menu.classList.toggle('active-menu')  
}

menuBtn.addEventListener('click', handlMenu)
closeBtn.addEventListener('click', handlMenu)
menuItems.forEach(menuItem => menuItem.addEventListener('click', handlMenu))


//        ,      :
modal.addEventListener('click', (e) => {
    if (!e.target.closest('.popup-content') || e.target.classList.contains('popup-close')) { 
        modal.style.display = 'none'
    }
})

//    ( ):
const animate = ({timing, draw, duration}) => { 

    let start = performance.now() 

    requestAnimationFrame(function animate(time) { 
        let timeFraction = (time - start) / duration 
        if (timeFraction > 1)  timeFraction = 1

        let progress = timing(1, timeFraction)

        draw(progress) 

        if (timeFraction < 1) {
            requestAnimationFrame(animate)
        }
        
    })
}
//    :
setTimeout(() => { //     1,5 ,    
    animate({
        duration: 3000,
        timing(x, timeFraction) {
            return Math.pow(timeFraction, 2) * ((x + 1) * timeFraction - x)
        },
        draw(progress) {
        //    block.style.left = 50 + '%' //      50% 
           popupContent.style.left = (40 * progress) + '%' //     
           popupContent.style.top = (25 * progress) + '%' //   
           popupContent.style.opacity = progress //    1,5  
        }
    })
}, 1000)